import { Divider } from '@mui/material'
import React from 'react'
import "./CalculatorOutput.css"
import {makeStyles} from '@mui/styles'
import { useNavigate } from "react-router-dom";
import { useSelector } from 'react-redux';
const useStyles=makeStyles({
    
    
    drawerDivider:{
        borderColor:"goldenrod !important",
        height:'1rem'
    },
})
const CalculatorOutput = () => {
     let navigate = useNavigate();
    const classes=useStyles()
    const goBack=()=>{
         navigate("/")
    }
function daysInMonth (month, year) {
     return new Date(year, month, 0).getDate();
}
let timeCalCulator=(sdate,edate)=>{
     let time={
          noOfMonths:0,
          noOfDays:0
     }
     if(edate.day-sdate.day>=0){
          time.noOfDays=edate.day-sdate.day;
          if(edate.month-sdate.month>=0){
               time.noOfMonths=edate.month-sdate.month
               if(edate.year-sdate.year>=0){
                    time.noOfMonths=time.noOfMonths+((edate.year-sdate.year)*12)
               }else{
                    console.log("invalidDate")
               }
          }else{
               time.noOfMonths=(edate.month+12)-sdate.month
               if((edate.year-1)-sdate.year>=0){
                    time.noOfMonths=time.noOfMonths+((edate.year-sdate.year)*12)
               }    
          }
     }else{
          
          if(daysInMonth(edate.month,edate.year)===31){
               time.noOfDays=(edate.day+31)-sdate.day;
               if((edate.month-1)-sdate.month>=0){
                    time.noOfMonths=(edate.month-1)-sdate.month
                    if(edate.year-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+((edate.year-sdate.year)*12)
                    }else{
                         console.log("Invaliddate")
                    }
               }else{
                    time.noOfMonths=((edate.month-1)+12)-sdate.month
                   
                    if((edate.year-1)-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+(((edate.year-1)-sdate.year)*12)
                    }   
               }
          }else if(daysInMonth(edate.month,edate.year)===30){
               time.noOfDays=(edate.day+30)-sdate.day;
               if((edate.month-1)-sdate.month>=0){
                    time.noOfMonths=(edate.month-1)-sdate.month
                    if(edate.year-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+((edate.year-sdate.year)*12)
                    }else{
                         console.log("Invaliddate")
                    }
               }else{
                    time.noOfMonths=((edate.month-1)+12)-sdate.month
                   
                    if((edate.year-1)-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+(((edate.year-1)-sdate.year)*12)
                    }   
               }
          }else{
               time.noOfDays=(edate.day+29)-sdate.day;
               if((edate.month-1)-sdate.month>=0){
                    time.noOfMonths=(edate.month-1)-sdate.month
                    if(edate.year-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+((edate.year-sdate.year)*12)
                    }else{
                         console.log("Invaliddate")
                    }
               }else{
                    time.noOfMonths=((edate.month-1)+12)-sdate.month
                   
                    if((edate.year-1)-sdate.year>=0){
                         time.noOfMonths=time.noOfMonths+(((edate.year-1)-sdate.year)*12)
                    }   
               }
          }
          
     }
     return time;

}
let calculatorInput=useSelector((state)=>state.setInput)
let actualdata={
     startdate:{
          day:Number((calculatorInput.startDate).slice(0,2)),
          month:Number((calculatorInput.startDate).slice(3,5)),
          year:Number((calculatorInput.startDate).slice(6,10)),
     },
     enddate:{
          day:Number((calculatorInput.endDate).slice(0,2)),
          month:Number((calculatorInput.endDate).slice(3,5)),
          year:Number((calculatorInput.endDate).slice(6,10)),
     }

}

function getIndianFormat(str) { 
     str = str.split(".");
     return str[0].replace(/(\d)(?=(\d\d)+\d$)/g, "$1,") + (str[1] ? ("."+str[1]): "");
    }
        
    console.log(getIndianFormat('100000'))
let intrestOfOneMonth=(principle,intrest)=>{
     return Math.round(principle*(intrest/100))
}
let intrestOfOneDay=(principle,intrest)=>{
    return Math.round(intrestOfOneMonth(principle,intrest)/30)
}

let timeData=timeCalCulator(actualdata.startdate,actualdata.enddate)
let totalIntrest=(intrestOfOneMonth(calculatorInput.principleAmt,Number(calculatorInput.intrest))*timeData.noOfMonths)+(intrestOfOneDay(calculatorInput.principleAmt,Number(calculatorInput.intrest))*timeData.noOfDays)
let totalIntrestOfallMonths=(intrestOfOneMonth(calculatorInput.principleAmt,Number(calculatorInput.intrest))*timeData.noOfMonths)
let intrestForSpecificDay=(x)=>{
     return (intrestOfOneDay(calculatorInput.principleAmt,Number(calculatorInput.intrest))*x)
}
    return (
        <div className="calculatorOutputContainer">
            <div className="calculatorOutput">
               <div className="mainCalculationOutput">
                    <div className="mainCalculatorOutput_upperOutputs mainCalculatorOutput__month__day">
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__month">
                             <h3>Months</h3>
                             <div className="block">
                              {timeData.noOfMonths}
                             </div>  
                        </div>
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__day">
                             <h3>Days</h3>
                             <div className="block">{timeData.noOfDays}</div> 
                        </div>
                        
                    </div>
                    {/* <Divider flexItem={false} className={classes.drawerDivider} /> */}
                    <div className="mainCalculatorOutput_upperOutputs mainCalculatorOutput__intrest__principle">
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__intrest">
                             <h3>Intrest</h3>
                             <div className="block">{getIndianFormat(totalIntrest.toString())}</div> 
                        </div>
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__principle">
                             <h3>Principle</h3>
                             <div className="block">{getIndianFormat(calculatorInput.principleAmt.toString())}</div> 
                        </div>
                    </div>
                </div>
                <div className="mainCalculatorOutput__total">
                            <h3>TOTAL</h3>
                            <div className="block">{getIndianFormat((totalIntrest+calculatorInput.principleAmt).toString())}</div>
                </div>
            </div>
            <Divider flexItem={false} className={classes.drawerDivider} />
            {/* /////////////////////////////////////////////////////// */}
            <div className="calculatorOutput__DayWise">
                
            <div className="mainCalculatorOutput_upperOutputs mainCalculatorOutput__intrest__principle">
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__intrest">
                             <h3>5Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(5)).toString())}</div> 
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(5))+calculatorInput.principleAmt).toString())}</div>
                        </div>
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__principle">
                             <h3>10Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(10)).toString())}</div>
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(10))+calculatorInput.principleAmt).toString())}</div> 
                        </div>
            </div>

            <div className="mainCalculatorOutput_upperOutputs mainCalculatorOutput__intrest__principle">
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__intrest">
                             <h3>15Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(15)).toString())}</div>
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(15))+calculatorInput.principleAmt).toString())}</div> 
                        </div>
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__principle">
                             <h3>20Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(20)).toString())}</div>
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(20))+calculatorInput.principleAmt).toString())}</div>
                        </div>
            </div>

            <div className="mainCalculatorOutput_upperOutputs mainCalculatorOutput__intrest__principle">
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__intrest">
                             <h3>25Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(25)).toString())}</div> 
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(25))+calculatorInput.principleAmt).toString())}</div>
                        </div>
                        <div className="mainCalculatorOutput_upperOutputs_block mainCalculatorOutput__principle">
                             <h3>30Days</h3>
                             <div className="block">{getIndianFormat((totalIntrestOfallMonths+intrestForSpecificDay(30)).toString())}</div>
                             <div className="block">{getIndianFormat(((totalIntrestOfallMonths+intrestForSpecificDay(30))+calculatorInput.principleAmt).toString())}</div> 
                        </div>
            </div>
                {/* {console.log(timeCalCulator({day:25,month:3,year:2021},{day:20,month:11,year:2021}))}     */}
            </div>
            
            <button onClick={goBack} className="calculatorInput__calculatorBtn">Go Back</button>
        </div>
    )
}

export default CalculatorOutput
