import React, { useState } from 'react'
import "./CalculatorInput.css"
import { Divider, MenuItem,Select } from '@mui/material'
import {makeStyles} from '@mui/styles'
import { useNavigate } from "react-router-dom";
import { useSelector,useDispatch } from 'react-redux';
import { setInput } from '../../action';
const useStyles=makeStyles({
    
    IntrestDropDown:{
        width: '200px',
        height:'1.5rem',
        borderColor: 'black !important',
        borderRadius:'5px !important',
        backgroundColor:'white !important,'
    },
    drawerDivider:{
        borderColor:"goldenrod !important",
        height:'1rem'
    },
})
var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
today = dd + '/' + mm + '/' + yyyy;

const intrestRateValue=[
    1,1.5,2,2.5,3,3.5,4,4.5,5,5.5,6,6.5,7,7.5,8,8.5,9.5,10,10.5,11,11.5,12,12.5,13,13.5,14,14.5,15,15.5,16,16.5
]
const CalculatorInput = () => {

    
    const [CalculatorData,SetCalculatorData]=useState({
        startDate:'',
        endDate:today,
        principleAmt:"",
        intrest:''
    })

    const handleChange=(e)=>{
       const {name,value} = e.target;
       if(name==="principleAmt"){
           if(typeof(value)=="string"){
               let modifiedAmt=Number(value.replace(/,/g,""))
               SetCalculatorData({
                   ...CalculatorData,
                   [name]:modifiedAmt
               })
           }
       }else{

        SetCalculatorData({
            ...CalculatorData,
            [name]:value
        })

       }
       
    }
    let calculate=()=>{
        dispatch(setInput(CalculatorData))
        navigate("/Output");
    }
    function getIndianFormat(str) { 
        str = str.split(".");
        return str[0].replace(/(\d)(?=(\d\d)+\d$)/g, "$1,") + (str[1] ? ("."+str[1]): "");
       }
    const classes=useStyles()
    let navigate = useNavigate();
    let dispatch=useDispatch()
    let calculatorInput=useSelector((state)=>state.setInput)
    return (
        <div className="calculatorInput">
        <div className="calculatorInput__form">
            <div className="StartingDate calculatorForm__allInputs">
            <label >Starting Date(dd/mm/yyyy)</label>
            <input onChange={handleChange} type="text" placeholder="Enter Starting Date" name="startDate" pattern="[0-9]{2}/[0-9]{2}/[0-9]{4}" value={CalculatorData.startDate} />
            <Divider className={classes.drawerDivider} />
            </div>
            {/* /// */}
            <div className="EndDate calculatorForm__allInputs">
            
            <label className="calculatorInput__form__lable" >End Date(dd/mm/yyyy)</label>

            <div className="EndDateSelaector">
           
            <input onChange={handleChange} name="endDate" pattern="[0-9]{2}/[0-9]{2}/[0-9]{4}" type="text" value={CalculatorData.endDate} />
            
            </div>
            <Divider className={classes.drawerDivider} />
            </div>
            {/* /// */}
            <div className="IntrestRate calculatorForm__allInputs">
            
            <label >Intrest Rate</label>
            <Select
            className={classes.IntrestDropDown}
          labelId="IntrestRate"
        //   id="demo-multiple-name"
        //   multiple
        name="intrest"
            value={CalculatorData.intrest}
            onChange={handleChange}
          >
          {intrestRateValue.map((item,index)=>(
            <MenuItem key={index} value={item}>{item}%</MenuItem>

          ))}
         </Select>
         <Divider className={classes.drawerDivider} />
            </div>
            {/* /// */}
            
            <div className="PrincipeAmount calculatorForm__allInputs">
           
            <label >Principe Amount</label>
            <input onChange={handleChange} name="principleAmt" type="text" value={getIndianFormat(CalculatorData.principleAmt.toString())} />
            </div>
            <button onClick={calculate} className="calculatorInput__calculatorBtn">Calculate</button>
            
        </div>
    </div>
    )
}

export default CalculatorInput
