import React, { useState } from 'react'
import "./Header.css"
import logo from "./prkLogo.jpeg"
import {Divider, Drawer, IconButton} from "@mui/material"
import SearchIcon from '@mui/icons-material/Search';
import MenuIcon from '@mui/icons-material/Menu';
import {makeStyles} from '@mui/styles'
import Box from '@mui/material/Box'

const useStyle=makeStyles({

    searchicon:{
        position:'relative',
        left:'-3rem',
        fill:'goldenrod !important'

    },
    Drawer:{
        '& .MuiDrawer-paper': {
            width: '240px',
            boxSizing: 'border-box',
            backgroundColor:'#1B1811 !important',
          },
    },
    drawerLogo:{
        objectFit:'contain',
        // width:'100%',
        borderRadius:'100%'
        
    },
    drawerDivider:{
        borderColor:"goldenrod !important",
        height:'1rem'
    },
    SeuHeaderBtn:{
        fill:'goldenrod !important',
        
    }

})
const Header = () => {
    const [drawerOpen,setDrawerOpen]=useState(false)
    const [subHeaderSearch,setSubHeaderSearch]=useState(false)
    const classes=useStyle()
    return (
       <>
        <div className="header">
            <div className="header__logoContainer">
            <img src={logo} />
            </div>

            <div className="header__siteName">
                <h1>Pramod Rambhau Kale Jewellers </h1>
            </div>
            <div className="header__SearchContainer">
                <input type="text" placeholder="Customer Name" name="searchContainer"/>
                <IconButton className={classes.searchicon}><SearchIcon /></IconButton>
            </div>
        </div>

        <div className="subHeader">
            <div className="subHeader__iconContainer"><IconButton  onClick={()=>{setDrawerOpen(true)}}><MenuIcon className={classes.SeuHeaderBtn} fontSize="large" /></IconButton></div>

            {subHeaderSearch?<div className="subheader__SearchContainer">
                <input type="text" placeholder="Customer Name" name="searchContainer"/>
                
            </div>:null}

            <div className="subHeader__iconContainer"><IconButton onClick={()=>{setSubHeaderSearch(!subHeaderSearch)}}><SearchIcon fontSize="large" className={classes.SeuHeaderBtn} /></IconButton></div>
            

           <Drawer
           variant='temporary'
           open={drawerOpen}
           onClose={()=>setDrawerOpen(false)}
           className={classes.Drawer}
           >
            <Box
             width="240px"
             height="110px"
             textAlign="center"
             paddingTop="1.2rem"
            >
            <img width="100px" height="100px" className={classes.drawerLogo} src={logo} />
            </Box> 
            <Divider variant="fullWidth" className={classes.drawerDivider} />
           </Drawer>
        </div>
       </>
    )
}

export default Header
